.InterViewMcqMain {
  padding: 20px 50px;
  //   font-family: "Poppins";

  .McqHeadingMain {
    color: #04724d;
    font-weight: bold;
    font-size: 34px;
    letter-spacing: -0.51px;
    text-align: left;
    // font: normal normal bold 34px/51px Poppins;
  }
  .McqQuestionArea {
    width: 100%;
    display: flex;
    margin-top: 15px;
    @media (min-width: 1536px) {
      justify-content: space-around;
    }
    @media (max-width: 1080px) {
      flex-wrap: wrap;
      .VideoArea {
        margin-top: 20px;
      }
    }
    .QuestionNumber {
      margin-top: 25px;
      text-align: left;
      font-weight: 600;
      font-size: 16px;
      letter-spacing: 0px;
      color: #000000;
      opacity: 1;
    }
    .QuestionArea {
      margin: 0 20px;
      width: calc(100% - 22% - 40px);
      max-width: 1000px;
      min-width: 600px;
      text-align: left;
      font-size: 14px;
      font-weight: 600;
      letter-spacing: 0px;
      color: #000000;
      opacity: 1;
      .QuestionText {
        // height: 230px;
        max-height: 300px;
        background-color: #fff;
        overflow: auto;
        margin-bottom: 5px;
        border-radius: 8px;
        padding: 20px;
        text-align: left;
        font-weight: 600;
        font-size: 15px;
        letter-spacing: 0px;
        color: #000000;
        opacity: 1;
      }
      .QuestionOptions {
        padding: 20px;
        // height: 250px;
        max-height: 300px;
        background-color: #fff;
        overflow: auto;
        border-radius: 8px;

        .OptionArea {
          border: 1px solid #dadada;
          border-radius: 4px;
          opacity: 1;
          margin-bottom: 10px;
          margin-right: 20px;
          display: flex;
          align-items: flex-start;
          .checkbox.MuiCheckbox-colorSecondary.Mui-checked {
            color: #04724d !important;
          }
          .checkbox.MuiCheckbox-root {
            color: #04724d !important;
          }
          .textLabel {
            padding: 9px 9px 9px 0;
          }
        }
      }
    }
    .VideoArea {
      width: 25%;
      max-width: 350px;
      min-width: 200px;
      display: flex;
      flex-direction: column;
      .VideoRecord {
        margin: 30px auto;
        position: relative;
        // height: 182px;
        // width: 322px;
        video {
          //   height: 100%;
          width: 100%;
          border-radius: 8px;
        }
        .audioDiv {
          padding: 10px 0;
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          bottom: 5px;
          position: absolute;
          background: #070707 0% 0% no-repeat padding-box;
          border-radius: 0px 0px 8px 8px;
          opacity: 0.31;
          .audio-img {
           width: 50%;
           display: flex;
           justify-content: flex-end;
          }
          .audio-analyser{
            width: 50%;
          }
          .volume {
            height: 5px;
          }
        }
      }
      .NextBtn {
        margin: 0 auto;
        color: #fff;
        opacity: 1;
        border-radius: 4px;
        box-shadow: none;
        .disabled {
          background: #abacad !important;
        }
        .normal {
          background: #04724d !important;
        }
      }
    }
  }
}
